* {
    box-sizing: border-box;
}

html,
body {
    margin: 0;
    padding: 0;
    font-family: Arial, Helvetica, sans-serif;
    background-color: #EDEFF3;
    display: flex;
    justify-content: center;
}

.map-container {
    margin-top: 60px;
    height: 720px;
    width: 720px;
    overflow: hidden;
    border-radius: 12px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
}