.h1 {
    margin: 30px 0;
    font-size: 24pt;
}

.ul {
    width: fit-content;
    margin: 0 0 30px 0;
    padding: 30px;
    display: flex;
    flex-direction: column;
    align-items: center;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
    border-radius: 12px;
}

.li {
    list-style: none;
    margin: 9px 0;
    display: flex;
    justify-content: center;
    align-items: center;
}

.a {
    color: black;
    margin: 0;
    padding: 30px;
    display: flex;
    justify-content: center;
    align-items: center;
    text-decoration: none;

    border: 1px solid #ddd;
    border-radius: 45px;
    user-select: none;
    cursor: pointer;

    &:hover {
        background-color: #007097;
        color: white;
    }
}