@use "global.module.scss" as *;

#sidebar {
    display: flex;
    flex-direction: column;
    position: relative;
    max-width: 210px;
}

#mobileContainer {
    background-color: $primaryAccent;
    position: absolute;
    top: 105px;
    left: -280px;
    padding: 21px;
    border-radius: 15px;
    box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.3);
    transition: all 0.1s ease-in-out;
    z-index: 2;
    opacity: 0;
}

#desktopContainer {
    background-color: white;
    padding: 12px 0 0 12px;
    z-index: 1;
    border-radius: 12px 12px 0 0;
}

.x {
    background-color: $primaryAccent;
    color: white;
    position: absolute;
    top: -24px;
    right: -24px;
    padding: 12px;
    font-size: 100%;
    width: 30px;
    height: 30px;
    z-index: -1;
    border-radius: 50%;
    cursor: pointer;

    &:hover {
        background-color: $primaryColor;
        scale: 1.08;
        transition: all 0.1s ease-in-out;
    }

    &:active {
        background-color: $primaryColor;
        scale: .92;
        transition: all 0.1s ease-in-out;
    }
}