 @use "global.module.scss" as *;

#wrapper {
    margin: 0;
    padding: 0;
    position: sticky;
    top: 0;
    width: 100%;
    display: flex;
    flex-direction: row;
    align-items: center;
    z-index: 10;
}

#bars {
    background-color: white;
    margin: 0 auto 0 9px;
    box-shadow: 0 1px 2px 1px rgba(0, 0, 0, .18);

    &:hover {
        background-color: $toolHover;
        transition: all .1s ease-in-out;
    }

    &:active {
        background-color: $toolActive;
        transition: all .1s ease-in-out;
    }
}

 #container {
     background-color: white;
     transform-origin: top right;
     margin: 0 0 0 auto;
     padding: 3px 9px;
     width: min-content;
     display: flex;
     flex-direction: row;
     align-items: center;
     border-radius: 0 12px 0 12px;
 }

 .shelf {
     background-color: white;
     display: flex;
     flex-direction: row;
     justify-content: space-evenly;
     align-items: center;
     border-radius: 12px;
 }

 .icon {
     position: relative;
     padding: 6px;
     width: 39px;
     height: 39px;
     border-radius: 12px;
     cursor: pointer;
     transition: all 0.1s ease-in-out;

     &:hover {
         background-color: $toolHover;
         scale: 1.1;
         transition: all 0.1s ease-in-out;
     }

     &:active {
         background-color: $toolActive;
         scale: 1;
         transition: all 0.1s ease-in-out;
     }

     @media screen and (max-width: 768px) {
         width: 21px;
         height: 21px;
         padding: 3px;
         border-radius: 6px;
     }
 }

 #faFilePdf {
     color: #973126;

     &:hover {
         color: $primaryColor;
     }
 }

 #faFileExcel {
     color: #0a9544;

     &:hover {
         color: $primaryColor;
     }
 }

 #faFilePowerpoint {
     color: #e74c3c;

     &:hover {
         color: $primaryColor;
     }
 }

 .tool {
     color: $toolColor;
     position: relative;
     padding: 9px;
     width: 30px;
     height: 30px;
     border-radius: 12px;
     cursor: pointer;
     transition: all 0.1s ease-in-out;

     &:hover {
         background-color: $toolHover;
         color: $primaryColor;
         scale: 1.1;
         transition: all 0.1s ease-in-out;
     }

     &:active {
         background-color: $toolActive;
         color: #005077;
         scale: 1;
         transition: all 0.1s ease-in-out;
     }

     @media screen and (max-width: 768px) {
         width: 21px;
         height: 21px;
         padding: 4px;
         border-radius: 6px;
     }
 }

 .resetDashboard {
     color: black;
     background-color: white;
     margin: 0 12px;
     height: 45px;
     width: 150px;
     display: flex;
     flex-direction: row;
     justify-content: center;
     align-items: center;
     font-weight: bold;
     font-size: 14pt;
     border: none;
     border-radius: 12px;
     box-shadow: 0 1px 2px 1px rgba(0, 0, 0, .18);
     cursor: pointer;
     user-select: none;
     transition: all 0.1s ease-in-out;

     &:hover {
         scale: 1.02;
         box-shadow: 0 2px 3px 1px rgba(0, 0, 0, .24);
         transition: all .1s ease-in-out;
     }

     &:active {
         scale: 1;
         box-shadow: inset 0 2px 2px 1px rgba(0, 0, 0, .18);
         transition: all 0.1s ease-in-out;
     }

     p {
         font-size: 18pt;
         margin: -3px 12px 0 0;
     }

     @media screen and (max-width: 768px) {
         margin: 6px;
         height: calc(30px * 0.8);
         width: calc(90px * 0.8);
         font-size: 9pt;
         border-radius: 9px;

         p {
             margin: 0px 6px 0 0;
             font-size: 9pt;
         }
     }
 }