@use "global.module.scss" as *;

#wrapper {
    background-color: white;
    margin: 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    border-radius: 12px;
    width: min-content;
}

#dashboard {
    color: black;
    margin: 0;
    padding: 0;
    font-size: 15px;
    font-family: Arial, Helvetica, sans-serif;
    display: flex;
    flex-direction: column;
    align-items: center;
    transform-origin: top left;
    border-radius: 12px;
    z-index: 0;
}

#mainContent {
    margin: 0;
    padding: 0;
    display: flex;
    flex-direction: row;
    border-radius: 12px;
}

#rightContainer {
    margin: 0;
    padding: 0;
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    border-radius: 12px;
    opacity: 0;
}

#tableauBlocker {
    background-color: white;
    margin: 0;
    position: absolute;
    bottom: 0px;
    height: 25px;
    width: 100%;
}