@use "global.module.scss" as *;

.spinner {
    position: absolute;
    top: 40%;
    width: 60px;
    height: 60px;
    border-radius: 50%;
    border: 6px solid $toolHover;
    border-top-color: $primaryColor;
    animation: spin 1s linear infinite;
}

@keyframes spin {
    0% {
        transform: rotate(0deg);
    }

    100% {
        transform: rotate(360deg);
    }
}